import React from 'react';
import hoistNonReactStatics from 'hoist-non-react-statics';
import { withStyles as withJSSStyles } from 'react-jss';
import theme from '../settings/theme';

const withStyles = styles => Component => {
  // Create a wrapper component that will inject the theme
  class WithStylesWrapper extends React.Component {
    render() {
      // Create the style function with theme
      const stylesFn = typeof styles === 'function' ? styles : () => styles;
      const StyledComponent = withJSSStyles(stylesFn)(Component);
      
      return <StyledComponent {...this.props} theme={theme} />;
    }
  }

  WithStylesWrapper.displayName = `WithStyles(${Component.displayName || Component.name || 'Component'})`;
  hoistNonReactStatics(WithStylesWrapper, Component);

  return WithStylesWrapper;
};

export { withStyles }; 