import React from 'react';
import PropTypes from 'prop-types';
import anime from 'animejs';

import { Layout } from '../../components/Layout';
import { Background } from '../../components/Background';
import { App } from '../../components/App';
import { Popup } from '../../components/Popup';

const navigation = [
  { path: '/about', label: 'About' },
  { path: '/journey', label: 'Journey' },
  { path: '/certifications', label: 'Certifications' },
  { path: '/blog', label: 'Blog' }
  ];

class Component extends React.Component {
  static displayName = 'Template';

  static propTypes = {
    location: PropTypes.object.isRequired,
    theme: PropTypes.object.isRequired,
    classes: PropTypes.object.isRequired,
    layout: PropTypes.object,
    background: PropTypes.object,
    children: PropTypes.any
  };

  static defaultProps = {
    layout: {},
    background: {}
  };

  constructor () {
    super(...arguments);

    this.state = {
      show: false,
      enterShow: false,
      enterAnimationShow: true,
      cursorPosition: { x: -100, y: -100 },
      cursorDotPosition: { x: -100, y: -100 },
      isHovering: false
    };
    this.cursorRef = React.createRef();
    this.cursorDotRef = React.createRef();
  }

  componentDidMount () {
    const { theme } = this.props;

    setTimeout(
      () => this.setState({ enterShow: true }),
      theme.animation.time
    );
    
    window.addEventListener('mousemove', this.handleMouseMove);
    window.addEventListener('mousedown', this.handleMouseDown);
    window.addEventListener('mouseup', this.handleMouseUp);
    this.addHoverListeners();
  }

  componentWillUnmount () {
    window.removeEventListener('mousemove', this.handleMouseMove);
    window.removeEventListener('mousedown', this.handleMouseDown);
    window.removeEventListener('mouseup', this.handleMouseUp);
    this.removeHoverListeners();
  }

  addHoverListeners = () => {
    const hoverElements = document.querySelectorAll('a, button, .clickable, [role="button"], input, textarea, select');
    hoverElements.forEach(element => {
      element.addEventListener('mouseenter', this.handleMouseEnter);
      element.addEventListener('mouseleave', this.handleMouseLeave);
    });
  };

  removeHoverListeners = () => {
    const hoverElements = document.querySelectorAll('a, button, .clickable');
    hoverElements.forEach(element => {
      element.removeEventListener('mouseenter', this.handleMouseEnter);
      element.removeEventListener('mouseleave', this.handleMouseLeave);
    });
  };

  handleMouseMove = (e) => {
    requestAnimationFrame(() => {
      this.setState({
        cursorPosition: { x: e.clientX, y: e.clientY },
        cursorDotPosition: { x: e.clientX, y: e.clientY }
      });
    });
  };

  handleMouseEnter = (e) => {
    const target = e.target;
    this.setState({ isHovering: true });

    // Add hovering class to cursor elements
    if (this.cursorRef.current) {
      this.cursorRef.current.classList.add('hovering');
    }
    if (this.cursorDotRef.current) {
      this.cursorDotRef.current.classList.add('hovering');
    }

    // Add highlight effect to hovered element
    target.style.transition = 'all 0.3s ease-out';
    target.style.transform = 'scale(1.02)';
    target.style.boxShadow = `0 0 20px ${this.props.theme.color.secondary.main}33`;
  };

  handleMouseLeave = (e) => {
    const target = e.target;
    this.setState({ isHovering: false });

    // Remove hovering class from cursor elements
    if (this.cursorRef.current) {
      this.cursorRef.current.classList.remove('hovering');
    }
    if (this.cursorDotRef.current) {
      this.cursorDotRef.current.classList.remove('hovering');
    }

    // Remove highlight effect from element
    target.style.transform = '';
    target.style.boxShadow = '';
  };

  handleMouseDown = () => {
    if (this.cursorRef.current) {
      this.cursorRef.current.style.transform = `translate(${this.state.cursorPosition.x}px, ${this.state.cursorPosition.y}px) scale(0.8)`;
    }
  };

  handleMouseUp = () => {
    if (this.cursorRef.current) {
      this.cursorRef.current.style.transform = `translate(${this.state.cursorPosition.x}px, ${this.state.cursorPosition.y}px) scale(1)`;
    }
  };

  onEnter = () => {
    const { theme } = this.props;

    this.setState({ enterAnimationShow: false });

    setTimeout(
      () => this.setState({ show: true }),
      theme.animation.time + theme.animation.stagger
    );
  }

  render () {
    const { show, enterShow, enterAnimationShow, cursorPosition, cursorDotPosition, isHovering } = this.state;
    const { location, classes, layout, background, children, theme } = this.props;

    const isURLContent = ['/blog', '/certifications', '/journey', '/about'].find(path => {
      return location.pathname.indexOf(path) === 0;
    });

    return (
      <Layout {...layout}>
        <div
          className={classes.cursor}
          ref={this.cursorRef}
          style={{
            transform: `translate(${cursorPosition.x}px, ${cursorPosition.y}px) scale(${isHovering ? 1.5 : 1})`,
            opacity: cursorPosition.x === -100 ? 0 : 1
          }}
        />
        <div
          className={classes.cursorDot}
          ref={this.cursorDotRef}
          style={{
            transform: `translate(${cursorDotPosition.x}px, ${cursorDotPosition.y}px)`,
            width: isHovering ? '8px' : '4px',
            height: isHovering ? '8px' : '4px',
            opacity: cursorDotPosition.x === -100 ? 0 : 1
          }}
        />
        <Background
          {...background}
          animation={{ show, ...background.animation }}
        >
          {isURLContent ? <App>{children}</App> : children}

          {!show && (
            <div className={classes.enterOverlay}>
              {enterShow && (
                <Popup
                  className={classes.enterElement}
                  ref={ref => (this.enterElement = ref)}
                  audio={{ silent: true }}
                  animation={{ independent: true, show: enterAnimationShow }}
                  message='LexiLominite wants to look cool by sounding cool!'
                  option='Enter'
                  onOption={this.onEnter}
                />
              )}
            </div>
          )}
        </Background>
      </Layout>
    );
  }
}

export { Component };
